"use client";

import { useState } from "react";
import { motion } from "framer-motion";
import LeftSideLayout from "@/components/common/Leftsidelayout";
import Stepper from "@/components/common/Stepper";
import "react-step-progress-bar/styles.css";

export default function PersonalDetailsPage() {
  const [profileImage, setProfileImage] = useState<string | null>(null);
  const currentStep = 1;
  const totalSteps = 6;

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => setProfileImage(reader.result as string);
      reader.readAsDataURL(file);
    }
  };

  return (
    <div className="h-screen w-screen overflow-hidden flex flex-col bg-[#f7f7f7]">
      {/* ‚úÖ NAVBAR */}
      <header className="w-full bg-white border-b border-gray-200 z-50 py-4 px-28">
        <div className="flex items-center space-x-4">
          <img src="/logo.png" alt="Workly Logo" className="h-6 w-auto" />
        </div>
      </header>

      {/* ‚úÖ MAIN SECTION */}
      <main className="flex flex-1 h-full m-0 p-0">
        {/* LEFT SIDE */}
        <LeftSideLayout />

        {/* RIGHT SIDE */}
        <div className="w-1/2 h-[calc(100vh-72px)] overflow-y-auto bg-[#f7f7f7] px-12 py-10 scrollbar-thin scrollbar-thumb-gray-300 scrollbar-track-transparent">
          <div className="w-full max-w-xl mx-auto flex flex-col min-h-full pb-12">
            
            {/* ‚úÖ Reusable Stepper Component */}
            <Stepper
              currentStep={currentStep}
              totalSteps={totalSteps}
              title="Personal Details"
            />

            {/* ‚úÖ FORM CARD */}
            <div className="bg-white rounded-2xl shadow-sm p-6 flex-grow flex flex-col justify-center">
              
              {/* Profile Upload */}
              <div className="flex flex-col items-center mb-5">
                <label htmlFor="profile-upload" className="cursor-pointer relative group">
                  <div className="w-16 h-16 rounded-full border-2 border-dashed border-gray-300 flex items-center justify-center overflow-hidden bg-gray-50 group-hover:border-blue-500 transition">
                    {profileImage ? (
                      <img
                        src={profileImage}
                        alt="Profile"
                        className="w-full h-full object-cover"
                      />
                    ) : (
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        fill="none"
                        viewBox="0 0 24 24"
                        strokeWidth={1.5}
                        stroke="gray"
                        className="w-8 h-8"
                      >
                        <path strokeLinecap="round" strokeLinejoin="round" d="M12 4v16m8-8H4" />
                      </svg>
                    )}
                  </div>
                  <input
                    type="file"
                    id="profile-upload"
                    accept="image/*"
                    className="hidden"
                    onChange={handleImageChange}
                  />
                </label>
                <p className="text-sm text-gray-600 mt-2">Add your photo to build trust</p>
              </div>

              {/* Form Inputs */}
              <form className="space-y-4 text-sm">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-gray-600 mb-1 font-bold">Full Name *</label>
                    <input
                      type="text"
                      placeholder="Enter your full name"
                      className="w-full border border-gray-300 rounded-lg px-3 py-2.5 focus:ring-2 focus:ring-blue-500 focus:outline-none"
                    />
                  </div>
                  <div>
                    <label className="block text-gray-600 mb-1 font-bold">Display Name *</label>
                    <input
                      type="text"
                      placeholder="Enter display name"
                      className="w-full border border-gray-300 rounded-lg px-3 py-2.5 focus:ring-2 focus:ring-blue-500 focus:outline-none"
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-gray-600 mb-1 font-bold">Email address</label>
                  <input
                    type="email"
                    placeholder="your@gmail.com"
                    className="w-full border border-gray-300 rounded-lg px-3 py-2.5 focus:ring-2 focus:ring-blue-500 focus:outline-none"
                  />
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-gray-600 mb-1 font-bold">Account Type</label>
                    <select className="w-full border border-gray-300 rounded-lg px-3 py-2.5 focus:ring-2 focus:ring-blue-500 focus:outline-none">
                      <option>Business</option>
                      <option>Individual</option>
                    </select>
                  </div>
                  <div>
                    <label className="block text-gray-600 mb-1 font-bold">Preferred Language</label>
                    <select className="w-full border border-gray-300 rounded-lg px-3 py-2.5 focus:ring-2 focus:ring-blue-500 focus:outline-none">
                      <option>English</option>
                      <option>Spanish</option>
                      <option>French</option>
                    </select>
                  </div>
                </div>

                <div>
                  <label className="block text-gray-600 mb-1 font-bold">Legal Business Name *</label>
                  <input
                    type="text"
                    placeholder="eg. Smith Plumbing LLC"
                    className="w-full border border-gray-300 rounded-lg px-3 py-2.5 focus:ring-2 focus:ring-blue-500 focus:outline-none"
                  />
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-gray-600 mb-1 font-bold">EIN Number</label>
                    <input
                      type="text"
                      placeholder="XX-XXX-XXXX"
                      className="w-full border border-gray-300 rounded-lg px-3 py-2.5 focus:ring-2 focus:ring-blue-500 focus:outline-none"
                    />
                  </div>
                  <div>
                    <label className="block text-gray-600 mb-1 font-bold">Company Type</label>
                    <select className="w-full border border-gray-300 rounded-lg px-3 py-2.5 focus:ring-2 focus:ring-blue-500 focus:outline-none">
                      <option>LLC</option>
                      <option>Partnership</option>
                      <option>Sole Proprietor</option>
                      <option>Corporation</option>
                    </select>
                  </div>
                </div>
              </form>
            </div>

            {/* ‚úÖ NEXT BUTTON */}
            <div className="flex justify-end pt-6 bg-[#f7f7f7] mt-6 rounded-b-2xl">
              <button
                type="button"
                className="bg-[var(--color-primary,#2563eb)] hover:opacity-80 text-white font-semibold px-10 py-2.5 rounded-lg transition"
              >
                Next
              </button>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
