"use client";

import { motion } from "framer-motion";
import Image from "next/image";

export default function LeftSideLayout() {
  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1 }}
      className="relative w-1/2 h-screen flex items-center justify-center overflow-hidden bg-[#f7f7f7]"
    >
      {/* üåÑ Background Image */}
      <motion.div
        initial={{ opacity: 0, scale: 1.05 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{ duration: 1.2, ease: "easeOut" }}
        className="absolute inset-0 z-0"
      >
        <Image
          src="/images/getstarted-leftside2.png"
          alt="Background"
          fill
          priority
          className="object-cover"
        />
      </motion.div>

      {/* üßç Foreground Images */}
      <div className="absolute inset-0 flex flex-col items-center justify-center z-10">
        {/* üë§ Guy Image (bigger now) */}
        <motion.div
          className="relative w-[300px] h-[300px] -ml-32"
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.6, duration: 0.8 }}
        >
          <Image
            src="/images/guy.png"
            alt="Guy"
            fill
            className="object-contain"
            priority
          />
        </motion.div>

        {/* üí∞ Earn Image (wider now) */}
        <motion.div
          className="relative w-[500px] h-[180px] mt-14 -ml-24"
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.9, duration: 0.8 }}
        >
          <Image
            src="/images/earn.png"
            alt="Earn"
            fill
            className="object-contain"
            priority
          />
        </motion.div>
      </div>
    </motion.div>
  );
}
